<template>
  <div class="card">
    <div class="card-header">Exam01RootState</div>
    <div class="card-body">
      <div>
        <h6>[Root State 읽기]</h6>
        <p>userId 단방향 바인딩: {{ $store.state.userId }}</p>
        <p>userId 단방향 바인딩: {{ store.state.userId }}</p>
        <!-- 함수호출하지 않고, getter의 이름만 넣는거임. -->
        <p>userId 단방향 바인딩: {{ store.getters.getUserId }}</p>
        <p>userId 단방향 바인딩: {{ getUserId() }}</p>
        <p>userId 단방향 바인딩: {{ computedUserId }}</p>
        <hr />
        <!-- 양방향이라 아래 input에서 바꾸면 userId가 쓰이는 곳에서 다 바뀜 -->
        <p>userId 양방향 바인딩: <input type="text" v-model="$store.state.userId" /></p>

        <hr />

        <h6>[Root State 변경]</h6>
        <p>userId 변경값 입력: <input type="text" v-model="userId" /></p>
        <div>
          <button @click="changeUserIdByMutation" class="btn btn-sm btn-info">userId 변경(Mutation 동기 방식)</button>
          <button @click="changeUserIdByAction" class="btn btn-sm btn-info">userId 변경(Action 비동기 방식)</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { useStore } from "vuex";
import { computed, ref } from "vue";

const store = useStore(); // 템플릿에서 $store 사용하는 방식 대신, 이렇게 자바스크립트로 가져올 수도 있음.
console.log(store.state.userId);

function getUserId() {
  // return store.state.userId; // 또는
  return store.getters.getUserId;
}

const computedUserId = computed(() => {
  // return store.state.userId; // 또는
  return store.getters.getUserId;
});

const userId = ref("");

function changeUserIdByMutation() {
  store.commit("setUserId", userId.value);
}
function changeUserIdByAction() {
  store.dispatch("setUserIdByAsync", { userId: userId.value, duration: 3000 });
}
</script>

<style scoped></style>
